.pulse {
  position: relative;
  padding: 30px 0;
}

.pulse-chart {
  height: 500px;
  padding: 20px;
  background-color: #fff !important;
}

.pulse-load {
  position: absolute;
  z-index: 999;
  width: 100%;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;


  & img {
    vertical-align: middle;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .pulse-chart {
    height: 300px;
    padding: 10px 0;
  }

  .pulse-load {
    height: 300px;
  }

}

