  .form-grid {
    flex-direction: column;
    margin: 0;
    padding-top: 30px;

    &_col {
      width: 100%;
      padding: 0;
    }

  }

}

/* iOS input inbox shadow fix */
input, textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

/* File upload */

.custom-file-upload-hidden {
  display: none;
  visibility: hidden;
  position: absolute;
  left: -9999px;
}
.custom-file-upload {
  display: block;
  width: auto;
  font-size: 14px;
  //margin-top: 30px;
  //border: 1px solid #ccc;
  label {
    display: block;
    margin-bottom: 5px;
  }
}

.file-upload-wrapper {
  display: flex;
  position: relative;
  //margin-bottom: 5px;
  //border: 2px solid #ccc;
  //border-radius: 4px;
}
.file-upload-input {
  width: 50%;
  padding: 10px 9px 10px 9px;
  font-family: $font-family;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.3;
  color: #000000;
  background-color: #ffffff;
  background-image: none;
  border: 2px solid #f5f5f5;
  border-radius: 4px;
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;

  &:hover, &:focus {
    outline: none;
  }

}
.file-upload-button {
  width: 50%;
  cursor: pointer;
  display: inline-block;
  color: #000;
  font-size: 14px;
  font-weight: 500;
  white-space: nowrap;
  padding: 5px 20px;
  border: none;
  margin-left: 5px;
  background-color: #EEEEEE;
  border-radius: 4px;
  transition: background-color .2s ease-in-out;
  outline: none;

  &:hover {
    background-color:#f5a623;
  }
}

